.single {
  .single-header {
    .cards {
      &__imagem {
        @media (max-width: 768px) {
          height: 360px;
        }
      }

      &__texto {
        @media (max-width: 768px) {
          margin-top: -85px;
          width: 90%;
        }

        .titulo {
          @media (max-width: 768px) {
            background: #fbf9f6;
            border-top: 6px solid #000000;
          }
          
          h3 {
            font-size: 30px;
            line-height: 125%;
            text-underline-offset: 3px;
          }

          p {
            font-family: $fonteTimes;
            font-weight: bold;
            font-size: 18px;
            letter-spacing: -0.01em;
          }
        }

        .categoria {
          a {
            @media (max-width: 768px) {
              font-size: 18px;
            }
          }
        }
      }
    }
  }

  .single-header-video {
    .cards {
      &__texto {
        margin-top: 0px;
        width: 100%;
        margin-left: 0px;

        .titulo {
          border-top: none;
          background: #000;
          width: 90%;
          float: right;

          h3 {
            font-size: 30px;
            color: #fff;
          }

          p {
            font-family: $fonteTimes;
            font-weight: bold;
            font-size: 20px;
            letter-spacing: -0.01em;
            color: #fff;
          }
        }
      }
    }
  }

  .single-content {
    @media (max-width: 768px) {
      padding: 0 30px;
    }

    p {
      font-family: $fonteTimes;
      font-size: 19px;
      line-height: 140%;
      margin-bottom: 30px;

      @media (min-width: 768px) {
        margin-left: 30px;
        font-size: 20px;
      }
    }

    h3 {
      font-family: $fontTitulo;
      font-size: 18px;
      margin-bottom: 0px;

      @media (min-width: 768px) {
        margin-left: 30px;
        margin-bottom: 5px;
      }
    }

    &__image {
      @media (max-width: 768px) {
        width: calc(100% + 60px);
        margin-left: -30px !important;
      }

      img {
        max-width: 585px;

        @media (max-width: 768px) {
          width: 100%;
        }
      }

      .image-detail {
        max-width: 450px;
        border-top: 6px solid #000000 !important;
        transform: translateY(-6px);
        line-height: 130%;

        @media (max-width: 768px) {
          width: calc(100% - 60px);
          margin: auto;
        }

        span {
          font-family: $fontTexto;
          font-weight: $light;
          font-size: 14px;
          letter-spacing: 0.01em;

          @media (min-width: 768px) {
            font-size: 15px;
          }

          &.credito {
            color: #939393;
          }
        }
      }
    }

    .wp-block-quote {
      border-top: 3px solid;
      border-bottom: 3px solid;
      margin-bottom: 30px;
      padding: 15px 0;

      @media (min-width: 768px) {
        width: 65%;
      }

      p {
        font-family: $fontTitulo;
        font-size: 22px;
        line-height: 130%;
        text-align: left;
        margin: 0;
      }
    }
  }
}

.pagina-sobre {
  h1 {
    font-family: $fontTitulo;
    font-size: 22px;
    letter-spacing: 0.02em;
    text-decoration: underline;
    text-decoration-color: #AA0404;
    text-decoration-thickness: 1px;
    text-underline-offset: 2px;

    @media (min-width: 768px) {
      font-size: 34px;
    }
  }

  .conteudo {
    p {
      font-family: $fonteTimes;
      font-size: 19px;
      line-height: 130%;

      @media (min-width: 768px) {
        font-size: 20px;
      }

      br {
        margin: 20px 0;
      }
    }
  }

  .sobre {
    width: 100%;
    margin-top: 30px;

    @media (min-width: 768px) {
      width: 70%;
      margin-top: 95px;
    }

    &__block {
      .block-open {
        border-top: 2px solid;
        border-bottom: 2px solid;

        h3 {
          font-family: $fontTitulo;
          color: #fff;
          background: #000;
          letter-spacing: 0.02em;
          font-size: 18px;
          line-height: 24px;
          padding: 3px 27px;
        }
        
        i {
          cursor: pointer;
          mask: url("data:image/svg+xml,%3Csvg width='10' height='17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.293 15.7L9.03 8.964a1.347 1.347 0 000-1.9L2.293.324a1.347 1.347 0 00-1.9 1.9l5.78 5.782L.394 13.8a1.347 1.347 0 000 1.9 1.347 1.347 0 001.9 0z' fill='%23000'/%3E%3C/svg%3E");
          -webkit-mask: url("data:image/svg+xml,%3Csvg width='10' height='17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2.293 15.7L9.03 8.964a1.347 1.347 0 000-1.9L2.293.324a1.347 1.347 0 00-1.9 1.9l5.78 5.782L.394 13.8a1.347 1.347 0 000 1.9 1.347 1.347 0 001.9 0z' fill='%23000'/%3E%3C/svg%3E");
          width: 10px;
          height: 17px;
          background-color: #aa0404;
          transform: rotate(90deg);
          margin: 6px 10px 0px 0;

          &.open {
            transform: rotate(-90deg);
          }
        }
      }

      .block-content {
        display: none;

        span {
          font-family: $fontTexto;
          color: #AA0404;
          letter-spacing: 0.02em;
          font-size: 20px;
          font-weight: $bold;
          min-width: 110px;
        }

        .texto {
          p {
            font-size: 18px;
            font-family: $fontTexto;
            font-weight: $light;
            line-height: 135%;
          }
        }
      }
    }
  }
}